.puzzle {
  margin: 20px 0;
  padding: 10px;
  background-color: #eef;

  z-index: 12;
  max-width: 400px;
  background-color: rgb(68, 19, 19);
  padding: 20px;
  border-radius: 10px;
  color: white;
  
}
.puzzle-image{
  width: 100%;
  border-top: 2px solid gray;
  border-bottom: 2px solid gray;
}
.puzzle p{
  color: white;
  text-shadow: 1px 1px rgb(0, 0, 0);
}
.puzzle h2{
  color: white;
}

.puzzle details{
  box-shadow: 5px 5px rgb(155, 154, 154);
  background-color:rgb(119, 105, 79);
  color: white;
  text-shadow: 1px 1px rgb(0, 0, 0);
  padding: 2px;
}
.puzzle summary:hover{
  color: rgb(0, 0, 0);
  background-color:rgb(119, 105, 79);
}

.puzzle input{
  width: 100%;
  padding: 5px;
}

.puzzle button{
  margin-top: 5px;
  margin-bottom: 5px;
  margin-left: 4px;
  margin-right: 4px;
  width: 45%;
}