.dialogue-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100;
}

.dialogueImg_conteiner{
  background: url('../../public/images/background7.jpg');
  border: 2px solid gray;
}

.dialogueImg{
  width: 30%;
  height: 200px;
  object-fit: cover;
  margin: 0px;
  padding: 0px;
}

.dialogueImgItem{
  width: 100%;
  height: 200px;
  object-fit: cover;

}

.dialogue {
  background: rgb(161, 37, 37);
  background: url('../../public/images/background5.jpg');
  padding: 20px;
  border-radius: 10px;
  width: 400px;
  max-width: 400px;
  text-align: center;
  color: rgb(0, 0, 0);
  border: 2px solid gray;
  text-shadow: 1px 1px rgb(255, 255, 255);
}

.dialogue-options {
  margin-top: 20px;
}

.dialogue-options button {
  display: block;
  width: 100%;
  margin: 10px 0;
  padding: 10px;
  /*background-color: rgb(19, 36, 68);*/
  background-color: rgb(68, 19, 19);
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-family: 'Times New Roman', Times, serif;
}

.dialogue-options button:hover {
  /*color: #00eeff;*/
  box-shadow: 5px 5px rgb(155, 154, 154);
  color: rgb(0, 0, 0);
  background-color:rgb(119, 105, 79);
}

.dialogueP{
  text-align: start;
}