.location {
  padding: 10px;
  border: 1px solid #ccc;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.head{
  font-size: 30px;
  color: rgb(0, 0, 0);
  text-shadow: 1px 1px rgb(255, 255, 255);
  margin-bottom: 5px;
  padding-bottom: 0px;
}

.locationImg{
  width: 100%;
  max-width: 1000px;
  height: 600px;
  object-fit: cover;
  border: 2px solid rgb(161, 161, 161);
  margin-bottom: 5px;
}

.description{
  color: white;
  background-color: rgba(0, 0, 0, 0.6);
  padding-top: 10px;
  padding-bottom: 10px;
}

.card {
  width: 140px;
  height: 200px;
  transition: 0.4s;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0px;
  position: relative; /* Родитель для абсолютного позиционирования */
  overflow: hidden; /* Чтобы содержимое не выходило за границы карточки */
}

.card:hover {
  transform: scale(1.2);
  z-index: 11;
}

.cardName {
  color: white;
  border-top: 1px solid black;
  margin: 0px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 10;
  position: absolute; /* Абсолютное позиционирование */
  bottom: 0; /* Прижимаем к нижней части */
  left: 0; /* Выравниваем по левому краю */
  font-size: 16px;
  padding: 8px; /* Добавляем отступы для текста */
  box-sizing: border-box; /* Учитываем padding в ширине */
  font-family: 'Times New Roman', Times, serif;
}


.actions{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1200px;
}

.cardImg{
  width: 100%;
  height: 100%;
  object-fit: cover;
}