/* Стили для инвентаря */
.inventory {
  position: fixed;
  top: 110px;
  right: 10px;
  background: white;
  padding: 10px;
  /*border: 1px solid #ccc;*/
}

.inventory {
  padding: 5px;
  /*background-color: rgb(19, 36, 68);*/
  background-color: rgb(68, 19, 19);
  border-radius: 10px;
  color: white;
}

.inventory h3 {
  margin-bottom: 10px;
  font-size: 24px;
  color: white;
  margin-top: 0px;
  border-bottom: 1px solid gray;
}

.inventory-items {
  display: flex;
  flex-wrap: wrap;
  gap: 1px;
  flex-direction: column;
}

.inventory-item {
  width: 140px;
  height: 100px;
  transition: 0.3s;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0px;
  position: relative; /* Родитель для абсолютного позиционирования */
  overflow: hidden; /* Чтобы содержимое не выходило за границы карточки */
  background: url('../../public/images/background5.jpg');
}

.inventory-item:hover {
    transform: scale(1.1);
    z-index: 11;
}

/* Стили для модального окна */

.inventory-modal {
  background-color: white;
  background: url('../../public/images/background5.jpg');
  padding: 20px;
  border-radius: 10px;
  max-width: 400px;
  width: 100%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.inventory-modal h2 {
  margin-bottom: 10px;
  font-size: 20px;
  color: rgb(0, 0, 0);
  text-shadow: 1px 1px rgb(255, 255, 255);
  border-bottom: 1px solid black;
}

.inventory-modal p {
  margin-bottom: 20px;
  color: rgb(0, 0, 0);
  text-shadow: 1px 1px rgb(255, 255, 255);
}

