/* Добавьте эти стили в существующий файл */

.inventory-item_i {
  position: relative; /* Для позиционирования счетчика */
  /* остальные существующие стили */
}

.item-count_i {
  position: absolute;
  bottom: 5px;
  right: 5px;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  font-weight: bold;
}

.item-count-info_i {
  color: #444;
  font-style: italic;
  margin-bottom: 10px;
}

/* Для карточек предметов */
.cardName_i {
  margin: 0;
  font-size: 12px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
}

.cardImg_i {
  width: 50px;
  height: 50px;
  object-fit: contain;
  margin: 5px auto;
}